.assurance-section {
  display: flex;
  flex-direction: row;
  overflow: hidden;
}

.assurance-section .img-holder img {
  height: 100%;
}

.assurance-section .assurance-section-content .assurance-inner {
  display: flex;
  flex-direction: row;
}

.assurance-section .assurance-section-content .assurance-inner .title {
  font-family: 'DINPro-Bold';
  color: #000;
  font-size: 18px;
  margin-bottom: 1rem;
}

.assurance-section
  .assurance-section-content
  .assurance-inner
  .assurance-col:first-child {
  width: 70%;
}

.assurance-section
  .assurance-section-content
  .assurance-inner
  .assurance-col:last-child
  ul {
  list-style: none;
}

.assurance-section
  .assurance-section-content
  .assurance-inner
  .assurance-col:last-child
  ul
  li {
  font-family: 'DINPro-Bold';
  color: #90292a;
  font-size: 16px;
  position: relative;
  margin-bottom: 0.1rem;
  cursor: pointer;
}

.assurance-section
  .assurance-section-content
  .assurance-inner
  .assurance-col:last-child
  ul
  li:hover {
  text-decoration: underline;
}

.assurance-section
  .assurance-section-content
  .assurance-inner
  .assurance-col:last-child
  ul
  li::after {
  content: '\e917';
  color: #90292a;
  border-radius: 50%;
  font-size: 16px;
  line-height: 28px;
  right: -33px;
  top: 50%;
  transform: translate(-50%, -50%);
  position: absolute;
  margin: 0 auto;
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@media only screen and (max-width: 1400px) {
  .assurance-section .img-holder {
    width: 844px;
  }
  .assurance-section .img-holder img {
    height: unset;
    width: 100%;
  }
}
@media only screen and (max-width: 992px) {
  .assurance-section {
    flex-direction: column;
  }
  .assurance-section .img-holder {
    width: 100%;
  }
}
@media only screen and (max-width: 768px) {
  .assurance-section .assurance-section-content .assurance-inner {
    flex-direction: column;
  }
  .assurance-section
    .assurance-section-content
    .assurance-inner
    .assurance-col:first-child {
    width: 100%;
  }
  .assurance-section
    .assurance-section-content
    .assurance-inner
    .assurance-col:last-child
    ul {
    list-style: none;
    padding-left: 0px;
  }
  .assurance-section
    .assurance-section-content
    .assurance-inner
    .assurance-col:last-child
    ul
    li::after {
    right: -8px;
  }
}
