.banner-section {
  overflow: hidden;
}

.banner-section .banner {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 372px;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
}

.banner-section .banner .banner-title {
  font-size: 40px;
  text-transform: uppercase;
  font-family: 'DINPro-Bold';
  margin-bottom: 0rem;
  color: #fff;
  max-width: 1000px;
  margin: 0 auto;
  position: relative;
  z-index: 2;
}

.banner-section .banner::before {
  content: ' ';
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  position: absolute;
  background-color: #00000040;
  margin: 0 auto;
  z-index: 1;
}

.banner-section .banner.content-left-banner {
  text-align: left;
}

.banner-section .banner.content-left-banner .banner-title {
  font-size: 25px;
  text-transform: inherit;
  font-family: 'DINPro-Bold';
  margin-bottom: 0rem;
  color: #fff;
  max-width: 440px;
  margin: 0 0;
  position: relative;
  z-index: 2;
}

.banner-section .banner.content-left-banner .btn-primary {
  color: #fff;
  font-size: 15px;
  padding: 13px 55px 13px 20px;
  line-height: 15px;
  opacity: 1;
  margin-bottom: 0px;
}

.banner-section .newsroom-banner-col {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.banner-section .newsroom-banner-col .cols-inner {
  width: 50%;
  flex-grow: 1;
}

.banner-section .newsroom-banner-col .cols-inner h2 {
  color: #90292a;
  margin-bottom: 1.3rem;
  font-size: 1.875rem;
}

.banner-section .newsroom-banner-col .cols-inner .img-holder img {
  width: 100%;
  max-width: 400px;
  margin: 0 auto;
  display: block;
}

@media only screen and (max-width: 1200px) {
  .banner-section .banner .banner-title {
    font-size: 26px;
    line-height: 33px;
  }
}

@media only screen and (max-width: 600px) {
  .banner-section .newsroom-banner-col {
    flex-direction: column;
  }
  .banner-section .newsroom-banner-col .cols-inner {
    width: 100%;
  }
  .banner-section .newsroom-banner-col .cols-inner:first-child {
    margin-bottom: 20px;
  }
  .banner-section .newsroom-banner-col .cols-inner h2 {
    font-size: 1.5625rem;
    margin-bottom: 0.9375rem;
  }
}

@media only screen and (max-width: 576px) {
  .banner-section .banner .banner-title {
    font-size: 22px;
    line-height: 29px;
  }
}
