.top-area {
  border-bottom: 1px solid #90292a;
  padding: 1.5px 0px;
}

.top-area .top-float {
  text-align: right;
}

.top-area .top-float .nav {
  list-style-type: none;
  display: inline-flex;
  padding: 0;
  margin: 0;
}

.top-area .top-float .nav li {
  display: inline-block;
  margin-bottom: 0px;
}

.top-area .top-float .nav li:last-child {
  margin-left: 15px;
}

.top-area .top-float .nav li.menu-item .search-block .search-input {
  height: 37px;
  background-color: #f6f6f6;
  border: 1px solid #f6f6f6;
  border-radius: 20px;
  width: 125px;
}

.top-area .top-float .nav li.menu-item .search-block .btn {
  right: 6px;
  top: -4px;
  height: 105%;
}

.top-area .top-float .nav li.menu-item .search-block:hover .search-input {
  background-color: #e8e9eb;
  border: 1px solid #e8e9eb;
  color: #a32a29;
}

.top-area .top-float .nav li.menu-item-has-children {
  position: relative;
  color: #90292a;
}

.top-area .top-float .nav li.menu-item-has-children::after {
  content: '\e91a';
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position: absolute;
  font-size: 20px;
  right: -1px;
  top: 51%;
  transform: translateY(-51%);
}

.top-area .top-float .nav li.menu-item-has-children .sub-menu {
  box-sizing: border-box;
  transition: all 0.15s cubic-bezier(0.25, 0, 0.25, 1.75), opacity 0.1s linear;
  background-color: #fff;
  padding: 3px 0;
  pointer-events: none;
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 99999;
  min-width: 100%;
  opacity: 1;
  pointer-events: auto;
  width: 100%;
}

.top-area .top-float .nav li.menu-item-has-children .sub-menu.sub-menu-lg {
  width: 300px;
  padding: 0px 10px;
}

.top-area .top-float .nav li.menu-item-has-children .sub-menu.sub-menu-lg li {
  text-align: left;
  border-bottom: 1px solid #f0f1f1;
}

.top-area .top-float .nav li.menu-item-has-children .sub-menu.sub-menu-lg li a {
  padding: 5px 10px;
  line-height: 23px;
}

.top-area
  .top-float
  .nav
  li.menu-item-has-children
  .sub-menu.sub-menu-lg
  li:last-child {
  border-bottom: 1px solid transparent;
}

.top-area .top-float .nav li.menu-item-has-children .sub-menu li {
  text-align: center;
  display: block;
}

.top-area .top-float .nav li.menu-item-has-children .sub-menu li a {
  display: block;
  color: #90292a;
}

.top-area .top-float .nav li.menu-item-has-children .sub-menu li:last-child {
  margin-left: unset;
}

.top-area .top-float .nav li a {
  color: #90292a;
  padding: 0px 20px 0px 10px;
  line-height: 41px;
  display: block;
  letter-spacing: 0.1px;
  cursor: pointer;
  position: relative;
  font-family: 'DINPro-Bold';
  font-size: 17px;
}

.top-area .top-float .nav li a::after {
  content: '';
  display: block;
  height: 2px;
  left: 50%;
  position: absolute;
  background: #90292a;
  transition: width 0.3s ease 0s, left 0.3s ease 0s;
  width: 0;
}

.top-area .top-float .nav li a:hover {
  text-decoration: none;
}

.top-area .top-float .nav li a:hover:after {
  width: 70%;
  left: 15%;
}

.top-area .top-float .nav li a.active:after {
  width: 70%;
  left: 15%;
}

.nav {
  display: flex;
  flex-wrap: wrap;
  padding-left: 0;
  margin-bottom: 0;
  list-style: none;
}
.search-block {
  position: relative;
  width: 130px;
  background-color: #fff;
}

.search-block .btn {
  padding: 10px;
  color: #90292a;
  border: none;
  background-color: transparent;
  position: absolute;
  right: 0px;
  top: 0px;
  height: 100%;
}

.search-block .btn span.icon {
  font-size: 16px;
  font-family: 'DINPro-Bold';
}

.search-block input[placeholder][class='search-input'] {
  color: #90292a;
}

.search-block .search-input {
  box-sizing: border-box;
  padding: 7px 35px 7px 10px;
  color: #90292a;
  outline: none;
  width: 100%;
  border: 1px solid #90292a;
  padding-right: 30px;
  border-radius: 40px;
}
.nav-section {
  position: relative;
  z-index: 9999;
  background-color: #fff;
}

.nav-section .col-tbl {
  display: flex;
  width: 100%;
  flex-direction: row;
  align-items: center;
}

.nav-section .col-tbl .cols a.logo {
  display: table;
}

.nav-section .col-tbl .cols a.logo img {
  display: table-cell;
  vertical-align: middle;
  max-width: 80px;
}

.nav-section .col-tbl .cols.left {
  width: 130px;
}

.nav-section .col-tbl .cols.right {
  width: calc(100% - 130px);
}

.nav-section .col-tbl .cols.right .nav {
