.products-section {
  background-color: #e0dddb;
}

.products-section .title {
  color: #90292a;
  font-family: 'DINPro-Medium';
  font-size: 25px;
  padding-right: 81px;
}

.products-section .product-card {
  background-color: #fff;
  border-radius: 10px;
  padding-bottom: 55px;
  position: relative;
}

.products-section .product-card .img-holder {
  overflow: hidden;
  border-radius: 10px 10px 0px 0px;
  height: 200px;
}

.products-section .product-card .img-holder img {
  width: 100%;
}

.products-section .product-card .card-content {
  padding: 20px 20px 0px;
}

.products-section .product-card .card-content .title {
  font-family: 'DINPro-Bold';
  color: #000;
  font-size: 18px;
}

.products-section .product-card .btn-box {
  padding: 0px 20px;
  position: absolute;
  bottom: 20px;
  width: 100%;
  left: 0px;
}

.products-section .product-card .btn-box .btn-primary {
  color: #fff;
  font-size: 15px;
  padding: 10px 65px 10px 20px;
  line-height: 15px;
  opacity: 1;
  margin-bottom: 0px;
  margin-top: 0px;
}

.products-carousel {
  padding-top: 2rem !important;
}

.products-carousel .owl-nav {
  display: flex;
  flex-direction: row;
  position: absolute;
  top: -42px;
  right: 0px;
}

.products-carousel .owl-nav .owl-prev {
  padding-right: 15px;
}

.products-carousel .owl-nav .icons {
  color: #90292a;
  font-size: 35px;
}

/**
 * Owl Carousel v2.2.1
 * Copyright 2013-2017 David Deutsch
 * Licensed under  ()
 */
/*
 *  Owl Carousel - Core
 */
.owl-carousel {
  display: none;
  width: 100%;
  -webkit-tap-highlight-color: transparent;
  /* position relative and z-index fix webkit rendering fonts issue */
  position: relative;
  z-index: 1;
}

.owl-carousel .owl-stage {
  position: relative;
  -ms-touch-action: pan-Y;
  -moz-backface-visibility: hidden;
  /* fix firefox animation glitch */
}

.owl-carousel .owl-stage:after {
  content: '.';
  display: block;
  clear: both;
  visibility: hidden;
  line-height: 0;
  height: 0;
}

.owl-carousel .owl-stage-outer {
  position: relative;
  overflow: hidden;
  /* fix for flashing background */
  -webkit-transform: translate3d(0px, 0px, 0px);
}

.owl-carousel .owl-wrapper,
.owl-carousel .owl-item {
  -webkit-backface-visibility: hidden;
  -moz-backface-visibility: hidden;
  -ms-backface-visibility: hidden;
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
}

.owl-carousel .owl-item {
  position: relative;
  min-height: 1px;
  float: left;
  -webkit-backface-visibility: hidden;
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
}

.owl-carousel .owl-item img {
  display: block;
  width: 100%;
}

.owl-carousel .owl-nav.disabled,
.owl-carousel .owl-dots.disabled {
  display: none;
}

.owl-carousel .owl-nav .owl-prev,
.owl-carousel .owl-nav .owl-next,
.owl-carousel .owl-dot {
  cursor: pointer;
  cursor: hand;
  user-select: none;
}

.owl-carousel.owl-loaded {
  display: block;
}

.owl-carousel.owl-loading {
  opacity: 0;
  display: block;
}

.owl-carousel.owl-hidden {
  opacity: 0;
}

.owl-carousel.owl-refresh .owl-item {
  visibility: hidden;
}

.owl-carousel.owl-drag .owl-item {
  user-select: none;
}

.owl-carousel.owl-grab {
  cursor: move;
  cursor: grab;
}

.owl-carousel.owl-rtl {
  direction: rtl;
}

.owl-carousel.owl-rtl .owl-item {
  float: right;
}

/* No Js */
.no-js .owl-carousel {
  display: block;
}

/*
     *  Owl Carousel - Animate Plugin
     */
.owl-carousel .animated {
  animation-duration: 1000ms;
  animation-fill-mode: both;
}

.owl-carousel .owl-animated-in {
  z-index: 0;
}

.owl-carousel .owl-animated-out {
  z-index: 1;
}

.owl-carousel .fadeOut {
  animation-name: fadeOut;
}

@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

/*
     * 	Owl Carousel - Auto Height Plugin
     */
.owl-height {
  transition: height 500ms ease-in-out;
}

/*
     * 	Owl Carousel - Lazy Load Plugin
     */
.owl-carousel .owl-item .owl-lazy {
  opacity: 0;
  transition: opacity 400ms ease;
}

.owl-carousel .owl-item img.owl-lazy {
  transform-style: preserve-3d;
}

/*
     * 	Owl Carousel - Video Plugin
     */
.owl-carousel .owl-video-wrapper {
  position: relative;
  height: 100%;
  background: #000;
}

.owl-carousel .owl-video-play-icon {
  position: absolute;
  height: 80px;
  width: 80px;
  left: 50%;
  top: 50%;
  margin-left: -40px;
  margin-top: -40px;
  background: url('owl.video.play.png') no-repeat;
  cursor: pointer;
  z-index: 1;
  -webkit-backface-visibility: hidden;
  transition: transform 100ms ease;
}

.owl-carousel .owl-video-play-icon:hover {
  transform: scale(1.3, 1.3);
}

.owl-carousel .owl-video-playing .owl-video-tn,
.owl-carousel .owl-video-playing .owl-video-play-icon {
  display: none;
}

.owl-carousel .owl-video-tn {
  opacity: 0;
  height: 100%;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: contain;
  transition: opacity 400ms ease;
}

.owl-carousel .owl-video-frame {
  position: relative;
  z-index: 1;
  height: 100%;
  width: 100%;
}

@media only screen and (max-width: 1200px) {
  .products-section .product-card .card-content .title {
    font-size: 16px;
  }
  .products-section .title {
    font-size: 23px;
  }
}

@media only screen and (max-width: 768px) {
  .products-carousel .owl-nav .icons {
    color: #90292a;
    font-size: 28px;
  }
}

@media only screen and (max-width: 600px) {
  .products-section .title.main-title {
    padding-right: 0px;
    margin-bottom: 58px;
    font-size: 20px;
  }
}

@media only screen and (max-width: 576px) {
  .products-carousel {
    padding-top: 1.1rem !important;
  }
}
