.mobile-menu ul {
  margin: 0;
  padding: 0;
}

.mobile-menu li {
  font-size: 13px;
  line-height: 24px;
  margin: 0;
  overflow: hidden;
  color: #221f1f;
  border-bottom: 1px solid #e7eaea;
  position: relative;
  text-align: left;
  text-transform: uppercase;
  padding-left: 15px;
}

.mobile-menu li.has-children span {
  position: absolute;
  right: 5px;
  top: 14px;
  font-size: 18px;
}

.mobile-menu li ul.children {
  background-color: #e8e9eb;
}

.mobile-menu li ul.children li {
  border-bottom: 1px solid #e7eaea;
  line-height: 24px;
}

.mobile-menu li:first-child {
  margin-top: 116px;
}

.mobile-menu li:hover {
  background-color: #e8e9eb;
}

.mobile-menu li a {
  text-decoration: none;
  color: #221f1f;
  display: inline-block;
  width: 100%;
  padding: 10px 5px;
}

/*---------------------
                Mobiles Menu - Slide IN 
                ----------------------*/
.mobile-menu {
  top: 0;
  max-width: 300px;
  right: -100%;
  width: 100%;
  background: #fff;
  color: black;
  height: 100%;
  position: fixed;
  z-index: 9997;
  padding: 0px 10px;
  overflow-y: auto;
  transform: translate3d(0, 0, 205px);
  transition: all 500ms ease-in-out;
}

.mobile-menu.active {
  right: 0;
  transform: translate3d(0, 0, 0);
  transition: all 500ms ease-in-out;
}

/*---------------------
                Mobiles Menu - Dropdown Submenu
                ----------------------*/
.has-children:hover {
  cursor: hand;
}

.children {
  display: none;
}

.mobile-menu .children li:first-child {
  margin-top: 0px;
}

