footer {
  background-color: #90292a;
  color: #fff;
  letter-spacing: normal;
}

footer h3 {
  font-size: 15px;
  margin-bottom: 20px;
  text-transform: uppercase;
  font-family: 'DINPro-Bold';
}

footer ul {
  list-style: none;
  margin-left: 0px;
  padding: 0;
}

footer ul li {
  font-size: 13px;
  margin-bottom: 5px;
}

footer ul li.footer-space {
  margin-bottom: 30px;
}

footer ul li.footer-space a {
  position: relative;
}

footer ul li.footer-space a:after {
  background-color: #fff;
  content: '';
  position: absolute;
  height: 1px;
  left: 0;
  bottom: -18px;
  width: 100%;
}

footer ul li img {
  margin-left: 8px;
}

footer ul li a {
  color: #fff;
}

footer ul li a:hover {
  color: #fff;
  text-decoration: underline;
}

footer .network-inner {
  display: inline-flex;
  flex-direction: row;
  margin-top: 20px;
}

footer .network-inner .network-col {
  display: block;
  margin-right: 25px;
  color: #000;
}

footer .network-inner .network-col:last-child {
  margin-right: 0px;
}

footer .network-inner .network-col .img-holder {
  width: 30px;
  height: 30px;
  border-radius: 50%;
}

footer .network-inner .network-col .img-holder img {
  width: 100%;
}

footer .network-inner .network-col p {
  margin: 0;
  color: #fff;
  font-size: 10px;
}

footer form input.form-control {
  border: 1px solid #d18d54;
  width: 100%;
  max-width: 238px;
  border-radius: 6px;
  height: 40px;
  font-size: 0.8em;
  text-align: center;
  padding: 0 15px;
  font-weight: 400;
  color: #fff;
  background-color: transparent;
}

footer form .btn-primary {
  background-color: #d18d54;
  font-size: 16px;
}

footer form .btn-primary::after {
  font-size: 11px;
  line-height: 21px;
  right: 1px;
  width: 20px;
  height: 20px;
}

footer ul.social-icons {
  list-style-type: none;
  padding: 0;
  margin: 0px 0px 0px 0px;
}

footer ul.social-icons li {
  display: inline-block;
  font-family: 'DINPro-Regular';
  margin-bottom: 0px;
}

footer ul.social-icons li a {
  display: inline-block;
  padding: 8px;
}

footer ul.policy-ul {
  list-style-type: none;
  padding: 0;
  margin: 0px 0 0px 0px;
}

footer ul.policy-ul li {
  display: inline-block;
  font-family: 'DINPro-Regular';
  margin-bottom: 0px;
}

footer ul.policy-ul li:first-child {
  position: relative;
}

footer ul.policy-ul li:first-child::after {
  background-color: #fff;
  content: '';
  position: absolute;
  height: 11px;
  right: 5px;
  bottom: 0px;
  top: 39%;
  width: 1px;
}

footer ul.policy-ul li a {
  display: inline-block;
  padding: 8px 15px 8px 0px;
  cursor: pointer;
}

footer ul.policy-ul li a:hover {
  text-decoration: underline;
}

footer a {
  font-family: 'DINPro-Regular';
  display: inline-block;
  font-size: 13px;
  cursor: pointer;
}

footer a.sitemap {
  margin-bottom: 20px;
}

footer a.sitemap:hover {
  text-decoration: underline;
}

@media only screen and (max-width: 576px) {
  footer h3 {
    font-size: 15px;
    margin-bottom: 10px;
  }
}
